header {
  text-align: center;
  background-color: #f5f5f5;
  border-bottom: 1px solid #ddd;
  width: 10%;
  padding: 20px;
}

header h1 {
  text-transform: uppercase;
  color: #333;
  margin: 0;
  font-size: 2rem;
}

nav {
  margin-top: 15px;
}

nav ul {
  justify-content: center;
  gap: 30px;
  margin: 0 auto;
  padding: 0;
  list-style: none;
  display: flex;
}

nav li {
  text-transform: uppercase;
  color: #555;
  cursor: pointer;
  border-bottom: 2px solid #0000;
  padding-bottom: 5px;
  font-size: 1rem;
  transition: border-color .3s;
}

nav li:hover {
  color: #000;
  border-bottom: 2px solid #45e9f5;
}

